<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h1>
      <button v-on:click="generate">あいうえお</button>
    </h1>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: '診断',
      counter: 0
    }
  },
  methods: {
    generate: function (event) {
      this.msg = 'hahaha' + this.counter
      this.counter += 1
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



